:root {
  --w-light: #b2b6c2;
  --w-medium: #757783;
  --w-dark: #3c3d40;
}

body {
  margin: 0;
  padding: 0;
}

#widgets-wrapper * {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#widgets-wrapper button {
  cursor: pointer;
}

#widgets-wrapper {
  height: 100vh;
  box-sizing: border-box;
  background: var(--w-dark);
  flex-direction: column;
  align-items: center;
  row-gap: 10px;
  padding: 10px;
  display: flex;
}

#widgets-wrapper .w-container {
  width: 100%;
  flex: 1;
  grid-template-columns: repeat(3, 1fr);
  grid-auto-rows: 1fr;
  gap: 10px;
  display: grid;
}

#widgets-wrapper .w-cell {
  height: 100%;
  flex: 1;
}

#widgets-wrapper .w-cell.dragover {
  background-color: var(--w-medium);
}

#widgets-wrapper #add-widget {
  background-color: var(--w-medium);
  color: #fff;
  width: 30px;
  height: 30px;
  border: none;
  padding: 0;
  font-size: 24px;
  line-height: 24px;
}

#widgets-wrapper #w-modal button {
  max-width: 300px;
  width: 100%;
  background: var(--w-medium);
  color: #fff;
  text-transform: uppercase;
  border: none;
  margin: 0 auto 10px;
  padding: 10px 20px;
  line-height: 1.5;
  display: block;
}

#widgets-wrapper #w-modal form {
  flex-direction: column;
  row-gap: 10px;
  display: flex;
}

#widgets-wrapper #w-modal form select {
  width: 100%;
  border: 1px solid var(--w-light);
  outline: none;
  padding: 10px 20px;
}

#widgets-wrapper #w-modal form button {
  max-width: 100%;
}

#widgets-wrapper #w-modal {
  justify-content: center;
  align-items: center;
  display: none;
  position: fixed;
  inset: 0;
}

#widgets-wrapper #w-modal__backdrop {
  opacity: .5;
  background: #000;
  position: absolute;
  inset: 0;
}

#widgets-wrapper #w-modal__content {
  max-width: 500px;
  width: 100%;
  background: #fff;
  margin: 0 20px;
  padding: 20px;
  position: relative;
}

#widgets-wrapper .widget {
  width: 100%;
  height: 100%;
  background: var(--w-light);
  border-radius: 20px;
  padding: 8px;
  display: block;
  position: relative;
}

#widgets-wrapper .widget.draggable {
  opacity: .3;
}

#widgets-wrapper .widget .w-control {
  width: 38px;
  height: 38px;
  background-color: var(--w-medium);
  color: #fff;
  z-index: 10;
  border: none;
  border: 5px solid var(--w-light);
  border-radius: 50%;
  outline: none;
  padding: 0;
  font-size: 28px;
  line-height: 1;
  display: none;
  position: absolute;
  top: -10px;
}

#widgets-wrapper .widget .w-control:disabled {
  color: #ffffff80;
  cursor: auto;
}

#widgets-wrapper .widget:hover .w-control {
  display: block;
}

.widget__remove-button {
  right: -10px;
}

.widget__update-button {
  right: 30px;
}

.widget__config-button {
  right: 70px;
}

#widgets-wrapper .widget.widget-valute {
  flex-direction: column;
  justify-content: space-between;
  display: flex;
}

#widgets-wrapper .widget.widget-valute .info {
  align-items: baseline;
  column-gap: 10px;
  margin-bottom: 10px;
  display: flex;
  position: relative;
}

#widgets-wrapper .widget.widget-valute .info .stats:after {
  content: "";
  border: 10px solid #0000;
  position: absolute;
  top: 50%;
  right: -30px;
  transform: translateY(-50%);
}

#widgets-wrapper .widget.widget-valute .info.growth .stats:after {
  border-bottom-color: green;
  border-right-color: green;
}

#widgets-wrapper .widget.widget-valute .info.fall .stats:after {
  border-bottom-color: red;
  border-left-color: red;
}

#widgets-wrapper .widget.widget-valute .valute {
  font-size: 32px;
  font-weight: 700;
}

#widgets-wrapper .widget.widget-valute .stats {
  font-size: 20px;
  position: relative;
}

#widgets-wrapper .widget.widget-valute .last_update {
  place-self: flex-end;
  margin: 0;
}

#widgets-wrapper .widget-time {
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: 700;
  display: flex;
}

#widgets-wrapper .widget-image__container {
  width: 100%;
  height: 100%;
  border-radius: 20px;
  position: relative;
  overflow: hidden;
}

#widgets-wrapper .widget-image__preloader {
  width: 100%;
  height: 100%;
  justify-content: center;
  align-items: center;
  display: flex;
  position: absolute;
  inset: 0;
}

#widgets-wrapper .widget-image__preloader:before {
  content: "";
  width: 40px;
  height: 40px;
  border: 4px solid var(--w-dark);
  border-left-color: #0000;
  border-radius: 50%;
  animation: 1s linear infinite rotate;
  position: relative;
}

@keyframes rotate {
  0% {
    transform: rotate(0);
  }

  100% {
    transform: rotate(360deg);
  }
}

#widgets-wrapper .widget-image__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  position: absolute;
  inset: 0;
}

